<template>
    <section>
        <div class="container-fluid mt-3 tab-style plaintabs">
            <loader :is-visible="isLoading"></loader>


            <div class="col-md-12 p-0 mt-2">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white">{{ $t('NewSocialPost') }}</span>
                    <span class="p-actions float-right">
                        <a href="javascript:;" value="cal" id="ShowCalender" v-on:click="BackToList()"
                            class="p-action-btn text-white" title="" data-toggle-tooltip="tooltip"
                            data-original-title="Back to List"><i class="fa fa-angle-double-left pr-2"></i><br /><span> Back
                                to List</span></a>
                    </span>
                </div>

            </div>

            <div class="row">
                <div class="col-md-12 p-0 mt-2 float-left">
                    <div class="col-sm-12 col-md-6 p-0 float-left position-relative">
                        <Form ref="AddPost">
                            <dynamic-form ref="AddPostForm" lang="en" :buttons="buttons" :schema="FormSchema">
                                <!-- v-on:OnSubmit="onSubmit"-->
                                <template v-slot:tgslot-Content="{ data }">
                                    <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value"></ckeditor>
                                    <!--:config="editorConfig"-->
                                    <span name="Content" class="invalid-feedback d-block">{{ data.error }}</span>
                                </template>
                            </dynamic-form>
                            <div v-if="isShowAdditionalBtns" class="col-12 float-left mt-3 modal-post">
                                <div class="float-left border bg-white">

                                    <a href="javascript:;" v-on:click="saveForm('PostNow')"
                                        class="float-left d-flex p-2 borderbottom w-100">
                                        <div class="float-left d-block">
                                            <em class="fa fa-paper-plane text-info p-3 font-24"></em>
                                        </div>
                                        <div class="float-left mt-2 ml-2">
                                            <span class="w-100 float-left font-weight-medium">Post Now</span>
                                            <span class="w-100 float-left font-12 font-weight-medium">Send out this post to
                                                the selected social channels/accounts.</span>
                                        </div>
                                    </a>
                                    <a href="javascript:;" v-on:click="SchedulePost('SchedulePost')"
                                        class="float-left d-flex p-2 borderbottom w-100">
                                        <div class="float-left d-block">
                                            <em class="fa fa-clock-o p-3 font-24" style="color:#1dbdac"></em>
                                        </div>
                                        <div class="float-left mt-2 ml-2">
                                            <span class="w-100 float-left font-weight-medium">Schedule Post</span>
                                            <span class="w-100 float-left font-12 font-weight-medium">Schedule a post for a
                                                later date or time.</span>
                                        </div>
                                    </a>
                                    <a href="javascript:;" v-on:click="SchedulePost('SendForApproval')"
                                        class="float-left d-flex p-2 borderbottom w-100">
                                        <div class="float-left d-block">
                                            <em class="fa fa-check-circle text-success p-3 font-24"></em>
                                        </div>
                                        <div class="float-left mt-2 ml-2">
                                            <span class="w-100 float-left font-weight-medium">Send Post For Approval</span>
                                            <span class="w-100 float-left font-12 font-weight-medium">Send for approval
                                                first with in your agency or organisation.</span>
                                        </div>
                                    </a>

                                    <a href="javascript:;" class="float-left d-flex p-2 w-100" v-on:click="DeletePost()">
                                        <div class="float-left d-block">
                                            <em class="fa fa-trash text-danger p-3 font-24"></em>
                                        </div>
                                        <div class="float-left mt-2 ml-2">
                                            <span class="w-100 float-left font-weight-medium">Delete</span>
                                            <span class="w-100 float-left font-12 font-weight-medium">Delete this post
                                                forever.</span>
                                        </div>
                                    </a>

                                </div>
                            </div>
                        </Form>
                    </div>
                    <div class="col-sm-12 col-md-6 p-0 float-left p-4 ">
                        <div class="bg-light p-3 float-left col-sm-12">
                            <div class="col-12  p-2">
                                <div class="col-12 d-flex align-items-center bg-white p-2">
                                    <span>Preview in</span>
                                    <span class="ml-2" v-if="PageFrom == 'FACEBOOK'"><em class="fa fa-facebook-square" style="font-size: 28px;color: #0067ff;"></em></span>
                                </div>
                                <div class=" bg-grey p-2 float-left col-sm-12">
                                    <div class="col-sm-12 col-md-12 float-left">
                                    <div class="float-left fb_pagelist mb-3 mt-3">
                                        <img class="float-left " src="/Content/images/hr_1.jpg">
                                        <span class="float-left ml-2 w-60">
                                            <span class="float-left w-100">{{ PageName }}</span>
                                            <small class="text-secondary float-left"> {{$options.filters.formatDate(CreatedDate)}}</small>
                                        </span>
                                    </div>
                                    </div>
                                    <div class="col-12  p-2 mt-2 float-left">
                                        <div v-html="FormSchema[1].Data[0].value"></div>
                                        <img class="img-fluid" v-for="imgSrc in fileData"
                                            v-bind:src="'data:image/gif;base64,' + imgSrc.base64String" />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <modalScheduleAndApprovePost v-if="isShowScheduleApproveModal" :ScheduleData="ScheduleData" :reqFrom="reqFrom"
                @close="isShowScheduleApproveModal = false"></modalScheduleAndApprovePost>
        </div>
    </section>
</template>

<script>
import DataService from '../../../../services/DataService';
import UploadAdapter from '../../../../assets/common/CKEditorFileRendor';
import modalScheduleAndApprovePost from '../../../../components/Common/Modal/modalScheduleAndApprovePost.vue';
export default {
    components: {
        modalScheduleAndApprovePost
    },
    data() {
        return {
            editor: this.CLASSIC_EDITOR,
            isLoading: false,
            AllPages: [],
            content: "<p>Some initial content</p>",
            editorConfig: {
                extraPlugins: [uploader],
            },
            File: null,
            fileData: [],
            schedule: [],
            PostId: 0,
            PageFrom: '',
            PageName:'',
            CreatedDate:'',
            PageRouteId: 0,
            ScheduleData: [],
            approverData: [],
            base64String: null,
            isShowAdditionalBtns: false,
            isShowScheduleApproveModal: false,
            reqFrom: '',
            scheduleDate: null,
            scheduleTime: null,
            approverId: 0,
            approverNote: '',
            PageTitle: '',
            ContainerName: null,
            IsDraft: false,
            UserId: 0,
            CompanyId: 0,
            TagData: [],
            TagName: '',
            UserTimeZone: '',
            ModuleName: 'CRM',
            base64StringData: '',
            blobPath: 'https://cogniterblob.blob.core.windows.net/',
            SubModuleCode: 'CRM_POST_SENT',
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('PostTo'),
                            name: "PostTo",
                            value: "",
                            placeholder: this.$t('Select'),
                            config: {
                                options: [],
                                onChange: this.SetPreviewData
                            },
                            validationRules: { "required": true }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('AssignCampaignOwner'),
                            name: 'AssignCampaignOwner',
                            value: "",
                            placeholder: this.$t('Select'),
                            config: {
                                options: []
                            },
                            validationRules: { "required": true }
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Content'),
                            name: "Content",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true }
                        },
                        {
                            astype: "FileField",
                            showLabel: true,
                            label: this.$t('Upload Imgae/Video'),
                            name: "Attachment",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 1,
                                accept: [
                                    {
                                        fileType: 'doc', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'docx', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'txt', // Text file type
                                        iconClass: 'fa-file-text-o' // Icon class for text files
                                    },
                                    {
                                        fileType: 'jpg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'jpeg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'png', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'gif', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'bmp', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'mp4', // Video types
                                        iconClass: 'fa-file-video-o' // Icon class for videos
                                    },
                                    {
                                        fileType: 'mov', // Video types
                                        iconClass: 'fa-file-video-o' // Icon class for videos
                                    },
                                    // Add more image, document, text, and video types as needed
                                ],
                                showSelectedFiles: [],
                                onChange: this.GetBase64String,
                                showAddIcon: false,
                                onAddButtonClick: "",
                                multiple: false
                            },
                        },
                        {
                            astype: "TextAreaField",
                            label: this.$t('FollowUpComment'),
                            name: "FollowUpComment",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Category'),
                            name: "Category",
                            value: "",
                            placeholder: "Select",
                            config: {
                                options: [],
                                showAddIcon: true,
                                onAddButtonClick: this.AddCategory,
                            },
                            validationRules: { "required": false },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Tags'),
                            name: "Tags",
                            value: "",
                            placeholder: "Select",
                            config: {
                                options: [],
                            },
                            validationRules: { "required": false },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Save for Later",
                    value: "Draft",
                    onClick: this.saveForm
                },
                {
                    type: "button",
                    class: "btn btn-primary",
                    text: "Post<i class='fa fa-angle-double-right pl-2 pr-2'></i>",
                    onClick: this.hideShowBtns
                },
            ],
        }
    },
    async created() {
        var vm = this;
        await vm.GetPageList();
        vm.FormSchema[1].Data[0].value = vm.content;
        vm.CompanyId = vm.GetUserInfo.CompanyId;
        vm.ContainerName = vm.GetUserInfo.ContainerName;
        vm.UserTimeZone = vm.GetUserInfo.TimeZone==undefined?'+00:00': vm.GetUserInfo.TimeZone ;
        vm.UserId = vm.GetUserInfo.ID;
        vm.PreviousId = this.$route.params.PrevId;
        vm.PageRouteId = this.$route.params.id == 0 ? 0 : atob(this.$route.params.id);
        if (vm.PageRouteId > 0 && this.$route.params.reqForm == 'Editcampaign') {
            await this.Editcampaign();
        } else {
            await this.FetchData();
        }
        await vm.GetCampaignOwners();
        await vm.GetTags();
        await vm.GetCategories();
        vm.$refs.AddPostForm.UpdateKeyValue();
    },
    methods: {
        AddCategory(){
            window.open('http://localhost:9000/Category/Index', '_blank');
        },
        FetchData: async function () {
            var vueObj = this;
            vueObj.isLoading = true;
            vueObj.PostId = vueObj.PageRouteId;
            var url = `id=${vueObj.PageRouteId}`;
            await DataService.GetSocialMediaPost(url).then(async function (response) {
                if (response.data != null && response.data != "") {
                    if (response.data.data.length > 0) {
                        var result = response.data.data[0];
                        (vueObj.FormSchema)[0].Data[0].value = result.ref_page_id;
                        (vueObj.FormSchema)[0].Data[1].value = result.campaign_owner_id;
                        (vueObj.FormSchema)[1].Data[0].value = result.content;
                        (vueObj.FormSchema)[1].Data[2].value = result.followup_content;
                        (vueObj.FormSchema)[1].Data[3].value = result.category_id;
                        (vueObj.FormSchema)[1].Data[4].value = result.tag_id;

                        var ScheduleObjData = {
                            start_date: result.start_date,
                            start_time: result.start_time,
                            approver_id: result.approver_id,
                            note: result.note,
                        };
                        vueObj.ScheduleData.push(ScheduleObjData);
                        if (result.attachement_path != null) {
                            vueObj.FormSchema[1].Data[1].config.showSelectedFiles.push({
                                attachmentName: result.attachement_name,
                                attachmentPath: vueObj.blobPath + '/' + vueObj.containerName + '/' + result.attachement_path,
                            });
                            var Posturl = `path=${result.attachement_path}`;
                            await DataService.GetBase64String(Posturl).then(function (response) {
                                vueObj.base64StringData = response.data;
                            })
                            var fileObj = {
                                attachement_mime: result.attachement_mime,
                                fileName: result.attachement_name,
                                userId: vueObj.UserId,
                                companyId: vueObj.CompanyId,
                                id: 0,
                                moduleName: vueObj.ModuleName,
                                subModuleCode: vueObj.SubModuleCode,
                                containerName: vueObj.ContainerName,
                                base64String: vueObj.base64StringData,
                                attachement_path: ''
                            };
                            vueObj.fileData.push(fileObj);
                        }
                        vueObj.AllPages.forEach(function (item) {
                            if (item.PAGE_ID == result.ref_page_id) {
                                if (item.ACCOUNT_NAME == 'FACEBOOK') {
                                    vueObj.PageFrom = 'FACEBOOK';
                                } else if (item.ACCOUNT_NAME == 'INSTAGRAM') {
                                    vueObj.PageFrom = 'INSTAGRAM';
                                }
                            }
                            vueObj.isLoading = false;
                        });
                    }
                }
                vueObj.isLoading = false;
            });
        },
        Editcampaign: async function () {
            var vueObj = this;
            vueObj.isLoading = true;
            var url = `id=${vueObj.PageRouteId}`;
            await DataService.GetCampaignData(url).then(async function (response) {
                if (response.data != null && response.data != "") {
                    if (response.data.data.length > 0) {
                        var result = '';
                        if (vueObj.PreviousId > 0) {
                            result = response.data.data;
                            response.data.data.forEach((item, index) => {
                                if (item.MainId == vueObj.PreviousId) {
                                    result = response.data.data[index];
                                }
                            })
                        } else {
                            result = response.data.data[response.data.data.length - 1];
                        }
                        if (result.PostId == null) {
                            vueObj.IsDraft = result.is_draft;
                            if (result.is_draft == false) {
                                vueObj.buttons[0].class += " disabled";
                                vueObj.buttons[1].class += " disabled";
                            }
                            vueObj.PostId = result.PostId == null ? result.MainId : result.PostId;
                            (vueObj.FormSchema)[0].Data[0].value = result.ref_page_id;
                            (vueObj.FormSchema)[0].Data[1].value = result.campaign_owner_id;
                            (vueObj.FormSchema)[1].Data[0].value = result.content;
                            (vueObj.FormSchema)[1].Data[2].value = result.followup_content;
                            (vueObj.FormSchema)[1].Data[3].value = result.category_id;
                            (vueObj.FormSchema)[1].Data[4].value = result.tag_id;

                            var ScheduleObjData = {
                                start_date: result.start_date,
                                start_time: result.start_time,
                                approver_id: result.approver_id,
                                note: result.note,
                            };
                            vueObj.ScheduleData.push(ScheduleObjData);
                            if (result.attachement_path != null) {
                                vueObj.FormSchema[1].Data[1].config.showSelectedFiles.push({
                                    attachmentName: result.attachement_name,
                                    attachmentPath: vueObj.blobPath + '/' + vueObj.containerName + '/' + result.attachement_path,
                                });
                                var Posturl = `path=${result.attachement_path}`;
                                await DataService.GetBase64String(Posturl).then(function (response) {
                                    vueObj.base64StringData = response.data;
                                })
                                var fileObj = {
                                    attachement_mime: result.attachement_mime,
                                    fileName: result.attachement_name,
                                    userId: vueObj.UserId,
                                    companyId: vueObj.CompanyId,
                                    id: 0,
                                    moduleName: vueObj.ModuleName,
                                    subModuleCode: vueObj.SubModuleCode,
                                    containerName: vueObj.ContainerName,
                                    base64String: vueObj.base64StringData,
                                    attachement_path: ''
                                };
                                vueObj.fileData.push(fileObj);
                            }
                        } else {
                            vueObj.PageRouteId = result.PostId;
                            vueObj.FetchData();
                        }
                        vueObj.AllPages.forEach(function (item) {
                            if (item.PAGE_ID == result.ref_page_id) {
                                if (item.ACCOUNT_NAME == 'FACEBOOK') {
                                    vueObj.PageFrom = 'FACEBOOK';
                                } else if (item.ACCOUNT_NAME == 'INSTAGRAM') {
                                    vueObj.PageFrom = 'INSTAGRAM';
                                }
                            }
                            vueObj.isLoading = false;
                        });
                    }
                }
                vueObj.isLoading = false;
            });
        },
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
            var divToRemove = document.querySelector('.ck-file-dialog-button');
            divToRemove.remove(); //for image remove
            let mediaIcon = document.getElementsByClassName('ck-dropdown')[2]
            mediaIcon.remove();   // for video icon remove
        },
        BackToList: function () {
            this.$router.push({ name: "CRMCampaign", params: { reqForm: 'AddPost' } });
        },
        DeletePost: function () {
            var vueObj= this;
            if (vueObj.PageRouteId == 0) {
                (vueObj.FormSchema)[0].Data[0].value = '';
                (vueObj.FormSchema)[0].Data[1].value = '';
                (vueObj.FormSchema)[1].Data[0].value = '';
                (vueObj.FormSchema)[1].Data[1].value = '';
                (vueObj.FormSchema)[1].Data[2].value = '';
                (vueObj.FormSchema)[1].Data[3].value = '';
                (vueObj.FormSchema)[1].Data[4].value = '';
                vueObj.fileData=[];
                vueObj.PageFrom='';
                vueObj.CreatedDate='';
                vueObj.PageName='';
            }else{
                vueObj.saveForm('Delete');
            }
        },
        SetPreviewData: function (event, id) {
            var vm = this;
            var SelectedPageValue = event.target.value;
            vm.PageFrom='';
            vm.CreatedDate='';
            vm.PageName='';
            vm.AllPages.forEach(function (item) {
                if (item.PAGE_ID == SelectedPageValue) {
                    if (item.ACCOUNT_NAME == 'FACEBOOK') {
                        vm.PageFrom = 'FACEBOOK';
                        vm.CreatedDate= item.CreatedDate;
                        vm.PageName= item.PAGE_NAME;
                    } else if (item.ACCOUNT_NAME == 'INSTAGRAM') {
                        vm.PageFrom = 'INSTAGRAM';
                    }
                }
            });
        },
        saveForm(type) {
            var vm = this;
            vm.isLoading = true;
            if (typeof (type) == 'object') {
                type = type.value;
            }
            let isDraft = false;
            if (type == 'Draft' || vm.IsDraft ==true) {
                isDraft = true;
            }
            if (type == 'SchedulePost' && vm.IsDraft == false && vm.PostId != 0) {
                vm.ShowAlert(vm.$t("AlreadyScheduled"), 'failure', true, vm.$t('Alert'));
                vm.isLoading = false;
                return false;
            }
            if (type == 'PostNow' && vm.IsDraft == false && vm.PostId != 0) {
                vm.ShowAlert(vm.$t("AlreadyPostUploaded"), 'failure', true, vm.$t('Alert'));
                vm.isLoading = false;
                return false;
            }
            vm.$refs.AddPost.validate().then(async result => {
                if (result) {
                    var url = '';
                    vm.schedule = [];
                    vm.approverData = [];
                    var CustomFieldArray = vm.$refs.AddPostForm.ConvertDynamicFormObjToCustomFieldObj(vm.FormSchema);
                    if(type=='PostNow'){
                        isDraft = false;
                    }
                    if (type == "SchedulePost") {
                        var ScheduleObj = {
                            start_date: vm.scheduleDate ? vm.$options.filters.formatDate(vm.scheduleDate) : vm.scheduleDate,
                            start_time: vm.scheduleTime ? vm.$options.filters.formatTime(vm.scheduleTime) : vm.scheduleTime,
                        }
                        vm.schedule.push(ScheduleObj);
                        isDraft = false;
                    }else{
                        vm.schedule=[];
                    }
                    if (type == "SendForApproval") {
                        var approverObj = {
                            start_date: vm.scheduleDate ? vm.$options.filters.formatDate(vm.scheduleDate) : vm.scheduleDate,
                            start_time: vm.scheduleTime ? vm.$options.filters.formatTime(vm.scheduleTime) : vm.scheduleTime,
                            approver_id: vm.approverId,
                            note: vm.approverNote,
                        }
                        vm.approverData.push(approverObj);
                        isDraft = false;
                    }else{
                        vm.approverData=[];
                    }
                    var Content = vm.removeHTMLTags(CustomFieldArray.filter(x => x.field_name == 'Content')[0].field_value);
                    var tagValue = CustomFieldArray.filter(x => x.field_name == 'Tags')[0].field_value;
                    vm.TagData.forEach(function (item) {
                        if (item.value == tagValue) {
                            vm.TagName = item.name;
                        }
                    });
                    var TagName = '';
                    if (vm.TagName != '') {
                        TagName = vm.TagName.replace(/\s/g, '');
                    }
                    var PageValue = CustomFieldArray.filter(x => x.field_name == 'PostTo')[0].field_value;
                    vm.AllPages.forEach(function (item) {
                        if (item.PAGE_ID == PageValue) {
                            vm.PageTitle = item.PAGE_NAME;
                        }
                    });
                    var PageTitle = vm.PageTitle;
                    var json_status_id=1001;
                    if(type=='Delete'){
                        json_status_id=1003;
                    }
                    var obj = {
                        post_id: vm.PostId == null ? 0 : vm.PostId,
                        ref_page_id: CustomFieldArray.filter(x => x.field_name == 'PostTo')[0].field_value,
                        campaign_owner_id: CustomFieldArray.filter(x => x.field_name == 'AssignCampaignOwner')[0].field_value,
                        content: Content,
                        followup_content: CustomFieldArray.filter(x => x.field_name == 'FollowUpComment')[0].field_value,
                        category_id: CustomFieldArray.filter(x => x.field_name == 'Category')[0].field_value,
                        tag_id: CustomFieldArray.filter(x => x.field_name == 'Tags')[0].field_value,
                        is_draft: isDraft,
                        schedule_date: vm.scheduleDate ? vm.$options.filters.formatDate(vm.scheduleDate) : vm.scheduleDate,
                        schedule_time: vm.scheduleTime ? vm.$options.filters.formatTime(vm.scheduleTime) : vm.scheduleTime,
                        schedule: vm.schedule,
                        approver: vm.approverData,
                        attachments: vm.fileData,
                        PostMessageAndTags: TagName == '' ? Content : Content + ' #' + TagName,
                        ref_Page_Title: PageTitle,
                        draft_status: vm.IsDraft,
                        json_status_id: json_status_id,
                        UserTimeZone: vm.UserTimeZone
                    }
                    if (type == "SchedulePost") {
                        var currentDate = new Date();
                        const inputDate = obj.schedule_date;
                        const inputTime = obj.schedule_time;
                        var timeComponents = inputTime.split(":");
                        var hours = parseInt(timeComponents[0]);
                        var minutes = parseInt(timeComponents[1].split(" ")[0]);
                        var meridian = timeComponents[1].split(" ")[1];
                        if (meridian === "PM" && hours < 12) {
                            hours += 12;
                        } else if (meridian === "AM" && hours === 12) {
                            hours = 0;
                        }
                        var dateComponents = inputDate.split("/");
                        var month = parseInt(dateComponents[0]) - 1;
                        var day = parseInt(dateComponents[1]);
                        var year = parseInt(dateComponents[2]);
                        var convertedDate = new Date(year, month, day, hours, minutes);
                        var formattedDateString = new Date(convertedDate)
                        var differenceInMilliseconds = formattedDateString - currentDate;
                        var differenceInMinutes = differenceInMilliseconds / (1000 * 60);
                        if (Math.abs(differenceInMinutes) < 20 && type == "SchedulePost") {
                            vm.ShowAlert((vm.$t('MinimumScheduleTime')), "warning", true, vm.$t("Alert"));
                            vm.isLoading = false;
                            return false;
                        }
                    }
                    url = JSON.stringify(obj);
                    let accountForPost = vm.AllPages.filter(x => x.PAGE_ID == obj.ref_page_id)[0].ACCOUNT_NAME;
                    if (accountForPost && !isDraft && type != "SendForApproval") {
                        if (accountForPost.toLowerCase() == 'facebook') {
                            await DataService.UploadFacebookPost(url).then(function (response) {
                                let jsonObject = JSON.parse(url);
                                var FB_Post_id = response.data.post_id == undefined ? response.data.id : response.data.post_id;
                                jsonObject.Fb_Post_id = FB_Post_id;
                                url = JSON.stringify(jsonObject);

                            })
                        } else if (accountForPost.toLowerCase() == 'instagram') {
                            // await DataService.UploadInstagramPost(url).then(function(response){
                            //     debugger;
                            // })
                        }
                    }
                    vm.scheduleDate = null;
                    vm.scheduleTime = null;
                    await vm.SaveSocialMediaPost(url, type);
                }
            })
        },
        uploadAdapterPlugin(editor) {
            var vm = this;
            editor.plugins.get('FileRepository').createUploadAdapter = (loader) => {
                return loader.file.then((file) => {
                    vm.File = file;
                })

            };
        },
        removeHTMLTags(text) {
            return text.replace(/<[^>]*>/g, '');
        },
        async GetPageList() {
            var vm = this;
            var url = 'TYPE=CRM_SOCIAL&MODULE_NAME=CRM';
            DataService.GetTriggerFieldValue(url).then(function (response) {
                vm.AllPages = response.data;
                if (vm.AllPages != null && vm.AllPages != undefined) {
                    vm.FormSchema[0].Data[0].config.options = [];
                    vm.AllPages.forEach(item => {
                        vm.FormSchema[0].Data[0].config.options.push({
                            name: item.NAME,
                            value: item.PAGE_ID
                        })
                    })
                }
            })
        },
        async GetCampaignOwners() {
            var vm = this;
            var url = "id=&moduleName=CRM&type=CRM_USERS";
            await DataService.getDDLData(url + "&search=").then(response => {
                if (response.data != '') {
                    var CampaignOwnerData = response.data.DATA;
                    var campDDLData = [];
                    if (CampaignOwnerData != null) {
                        vm.FormSchema[0].Data[1].config.options = [];
                        CampaignOwnerData.forEach(x => {
                            campDDLData.push({
                                name: x.name,
                                value: x.value
                            })
                        })
                        vm.FormSchema[0].Data[1].config.options = campDDLData;
                        vm.FormSchema[0].Data[1].value = vm.UserId
                    }
                }
            });
        },
        async GetTags() {
            var vm = this;
            var url = "id=&moduleName=CRM&type=CRM_TAGS";
            await DataService.getDDLData(url + "&search=").then(response => {
                if (response.data != '') {
                    vm.TagData = response.data.DATA;
                    var tagDDLData = [];
                    if (vm.TagData != null) {
                        vm.FormSchema[1].Data[4].config.options = [];
                        vm.TagData.forEach(x => {
                            tagDDLData.push({
                                name: x.name,
                                value: x.value
                            })
                        })
                        vm.FormSchema[1].Data[4].config.options = tagDDLData;
                    }
                }
            });
        },
        async GetCategories() {
            var vm = this;
            var url = "id=&moduleName=CRM&type=CRM_CAMPAIGN_CATEGORY";
            await DataService.getDDLData(url + "&search=").then(response => {
                if (response.data != '') {
                    var Categories = response.data.DATA;
                    var catDDLData = [];
                    if (Categories != null) {
                        vm.FormSchema[1].Data[3].config.options = [];
                        Categories.forEach(x => {
                            catDDLData.push({
                                name: x.name,
                                value: x.value
                            })
                        })
                        vm.FormSchema[1].Data[3].config.options = catDDLData;
                    }
                }
            });
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];
                var files = evt.target.files; // FileList object
                for (var i = 0; i < files.length; i++) {
                    var f = files[i];
                    var reader = new FileReader();
                    // Closure to capture the file information.
                    reader.onload = (function (theFile) {
                        return function (e) {
                            var binaryData = e.target.result;
                            //Converting Binary Data to base 64
                            var base64StringEncoded = window.btoa(binaryData);
                            vm.base64String = base64StringEncoded;
                            //showing file converted to base64

                            var fileName = f.name;
                            vm.fileName = fileName;
                            var n = fileName.lastIndexOf(".");
                            var extensionName = fileName.substring(n + 1, fileName.length);

                            vm.fileExt += "." + extensionName;
                            var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                            var fileSize = f.size;
                            vm.fileSize = fileSize;
                            var fileObj = {
                                attachement_mime: attachemnetMime,
                                fileName: fileName,
                                userId: vm.UserId,
                                companyId: vm.CompanyId,
                                id: 0,
                                moduleName: vm.ModuleName,
                                subModuleCode: vm.SubModuleCode,
                                containerName: vm.ContainerName,
                                base64String: base64StringEncoded,
                                attachement_path: ''
                            };

                            vm.fileData.push(fileObj);
                        };
                    })(f);
                    // Read in the image file as a data URL.
                    reader.readAsBinaryString(f);
                }
            }
            catch (ex) {
            }
        },
        hideShowBtns() {
            var vm = this;
            vm.isShowAdditionalBtns = vm.isShowAdditionalBtns ? false : true;
        },
        async SaveSocialMediaPost(url, type) {
            var vm = this;
            await DataService.SaveSocialPost(url).then(async function (response) {
                if (response.data.MAIN_ID > 0) {
                    if (vm.fileData.length > 0) {
                        vm.fileData.forEach(x => {
                            x.id = response.data.MAIN_ID;
                        })
                        var formData = JSON.stringify(vm.fileData);
                        await DataService.UploadFile(formData).then(function (response) {
                    })
                    }
                    vm.isLoading = false;
                    if (type == 'PostNow') {
                        vm.ShowAlert(vm.$t([vm.$t('PostUploaded')]), "success", true, vm.$t("Alert"));
                    } else if (type == 'SchedulePost') {
                        vm.ShowAlert(vm.$t([vm.$t('PostScheduled')]), "success", true, vm.$t("Alert"));
                    } else if (type == 'SendForApproval') {
                        vm.ShowAlert(vm.$t([vm.$t('PostSentApproval')]), "success", true, vm.$t("Alert"));
                    } else if (type == 'Draft') {
                        vm.ShowAlert(vm.$t([vm.$t('PostSaveAsDraft')]), "success", true, vm.$t("Alert"));
                    }
                    var currentDate = new Date();
                    var year = currentDate.getFullYear();
                    var month = (currentDate.getMonth() + 1).toString().padStart(2, '0');
                    var day = currentDate.getDate().toString().padStart(2, '0');
                    var formattedDate = year + "-" + month + "-" + day;

                    var ids = vm.approverId == 0 ? vm.GetUserInfo.ID : vm.approverId;
                    var Notificationobj = {
                        // message: "Test^" + vm.GetUserInfo.Name,
                        message: vm.PageName +'^' + formattedDate + '^' + vm.GetUserInfo.Name + '^' + ids,
                        notificationFor: ids,
                        typeCode: vm.approverId ==0 ? "CRM_CAMPAIGN_INSERT" : "CRM_APPROVAL_RAISED_BY",
                        referenceId: response.data.CAMPAIGN_ID ? response.data.CAMPAIGN_ID : response.data.MAIN_ID,
                        referenceId2: 0
                    }
                    await DataService.InsertNotification(JSON.stringify(Notificationobj)).then(function (response) {
                        //for Insert the notification 
                    })
                    vm.$router.push({ name: "CRMCampaign" });
                }
                vm.isLoading = false;
            })
        },
        SchedulePost(reqFrom) {
            var vm = this;
            vm.reqFrom = reqFrom;
            vm.isShowScheduleApproveModal = true;
        }
    }
}
export const uploader = function (editor) {
    editor.plugins.get('FileRepository').createUploadAdapter = (loader) =>
        new UploadAdapter(loader);
};

